body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body
{
background-color:#e9e9e9;
margin:0;
}
.card{
    margin: 20px;
}
.container {
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.input {
    padding: 10px;
    text-align: center;
}
.title {
    font-size: 40px;
    margin-top: 20px;
}

.gridContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;

}
.switchesContaier{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.autoComplete{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-top: 2px;
}

.serachResContainer{
    border: 1px solid black;
    width: 220px;
    margin-right: 39px;
    margin-top: -5px;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.singleSearchres {
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
}
.firstAutoComp{
    border: 1px solid black;
    width: 220px;
    margin-right: 39px;
    margin-top: -5px;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.searchContainer{
    width: 100%;
}
.defaultLocationCard{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-right: 35px;
}




.GridItem{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
}
